<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a v-show='page !== 1' class="pagination__link pagination__link--arrow" href='#' aria-label="Предыдущая страница" @click.prevent='paginate(page-1)'>
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for='pageNumber in pages' :key='pageNumber'>
      <a href="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click.prevent='paginate(pageNumber)'>
        {{pageNumber}}
      </a>
    </li>

    <li class="pagination__item">
      <a  v-show='pages > page' class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница" @click.prevent='paginate(page+1)'>
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>

export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
    previousPage(page) {
      return page <= 1;
    },
    nextPage(page) {
      return page >= Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
  },
};
</script>
